@import url(https://fonts.googleapis.com/css2?family=Bigelow+Rules&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}h1{font-family:'Bigelow Rules', cursive;font-size:72px;color:#ea5b1e}

.card-container{display:flex;flex-direction:column;border:1px solid #ea5b1e;border-radius:5px;padding:25px;cursor:pointer;-moz-osx-font-smoothing:grayscale;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transfrom 0.5s ease-out;color:#ea5b1e}.card-container:hover{transform:scale(1.04)}

.card-list{width:85vw;margin:auto;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:20px}

.search{-webkit-appearance:none;border:none;border-bottom:1px solid #ea5b1e;outline:none;padding:10px;min-width:150px;line-height:30px;margin-bottom:30px;width:130px;transition:width 0.4s ease-in-out}.search:focus{width:350px}

